@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@400;500;700&display=swap);
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'IBM Plex Sans', sans-serif;
}

.wrapper {
  width: 100%;
  margin: 0 auto;
}

.button {
  cursor: pointer;
  width: 100%;
  min-height: 56px;
  font-weight: 500;
  font-size: 16px;
  color: #EBF4F8;
  background: #0880AE;
  box-shadow: 0 2px 4px rgba(44, 39, 56, 0.08), 0 4px 8px rgba(44, 39, 56, 0.08);
  border: none;
  border-radius: 6px;
  transition: 0.2s;
}

.items-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

input[placeholder] {
    text-overflow:ellipsis;
    transition: 0.2s;
}

::-webkit-input-placeholder {
    color: #7C9CBF;
    text-overflow:ellipsis;
    -webkit-transition: 0.2s;
    transition: 0.2s;
}

::-moz-placeholder {
    color: #7C9CBF;
    text-overflow:ellipsis;
    -moz-transition: 0.2s;
    transition: 0.2s;
}

:-moz-placeholder {
    color: #7C9CBF;
    text-overflow:ellipsis;
    -moz-transition: 0.2s;
    transition: 0.2s;
}

:-ms-input-placeholder {
    color: #7C9CBF;
    text-overflow:ellipsis;
    -ms-transition: 0.2s;
    transition: 0.2s;
}

:focus::-webkit-input-placeholder {transform: translateY(30px)}
:focus::-moz-placeholder          {transform: translateY(30px)}
:focus:-moz-placeholder           {transform: translateY(30px)}
:focus:-ms-input-placeholder      {transform: translateY(30px)}

.global {
    background-color: #fff;
}

.global .wrapper {
    padding: 40px 30px 10px 30px;
}

.registration__title {
    margin: 0 0 10px 0;
    font-weight: bold;
    font-size: 34px;
    line-height: 44px;
    color: #2C2738;
}

.registration__account {
    display: flex;
    align-items: center;
}

.registration__description {
    display: inline-block;
    padding: 0 0 3px 0;
    font-size: 16px;
    color: #2C2738;
}

.registration__btn {
    width: auto;
    width: initial;
    padding: 3px 6px;
    font-size: 16px;
    min-height: 22px;
    box-shadow: none;
    color: #0880AE;
    background-color: transparent;
}

.registration__btn:hover {
    color: #756F86;
}
.form__group {
    margin: 58px 0 0 0;
}

.form__title {
    margin: 7px 0;
    font-weight: 500;
    font-size: 16px;
    color: #756F86;
}

.form__control {
    border-radius: 6px;
    border: 1px solid #DBE2EA;
}

.form__input {
    position: relative;
    outline: none;
    width: 100%;
    padding: 11px;
    font-size: 16px;
    color: #2C2738;
    background: #FFFFFF;
    border: 2px solid transparent;
    box-shadow: 0 4px 8px rgba(44, 39, 56, 0.04);
    border-radius: 6px;
}

input:-webkit-autofill {
    border: 2px solid #DBE2EA;
    border-radius: 6px;
    -webkit-box-shadow:0 0 0 50px white inset; /* можно изменить на любой вариант цвета */
    -webkit-text-fill-color: #333;
    -webkit-transition: 0.3s;
    transition: 0.3s;
}

.form__input:focus {
    border: 2px solid #18a0fb;
}

.form__input_validation {
    display: inline-block;
    margin: 8px 0;
    font-size: 14px;
    color: #FF7171;
    transition: 0.3s;
}

.form__input_error {
    visibility: visible;
    opacity: 1;
}

.form__input_waiting {
    visibility: hidden;
    opacity: 0;
}

.form__input_ok {
    visibility: hidden;
    opacity: 0;
}
